body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 900px) {
  .contenedor {
    max-width: 100rem;
  }
}

.bg-custom {
  background-image: url(/build/img/punto.webp);
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  margin: 0;
}

p {
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
}

.gradiente-letras {
  background-image: linear-gradient(90deg, #1DF1EF, #EF1DF1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  display: flex;
  justify-content: center;
}

.sombra {
  border: 1px solid #c8c8c8;
  box-shadow: 5px 5px 13px #959595;
  margin-bottom: 2rem;
}

.boton-amarillo-block {
  display: block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-amarillo {
  display: inline-block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-verde {
  display: inline-block;
  background-color: #71B100;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-rojo {
  display: inline-block;
  background-color: red;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #e60000;
}

.ocultar-boton {
  display: none;
}

/** UI **/
header {
  background-color: #094746;
  height: 8rem;
  border-bottom: 0.1rem solid #0c5e5c;
}
header .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 2rem;
}
@media (min-width: 1260px) {
  header .header {
    padding: 0;
  }
}
header .header .logo {
  width: 17rem;
  min-width: 15rem;
  cursor: pointer;
}
header .header-nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
header .header-nav .header-desktop {
  display: none;
}
@media (min-width: 768px) {
  header .header-nav .header-desktop {
    display: block;
  }
}
header .header-nav .header-desktop .contenedor-nav {
  display: flex;
}
header .header-nav .header-desktop .contenedor-nav li {
  margin-right: 4rem;
  font-size: 1.6rem;
  font-weight: 500;
}
header .header-nav .header-desktop .contenedor-nav a {
  color: #1DF1EF;
}
header .header-nav .modos {
  cursor: pointer;
  display: flex;
  align-items: center;
  min-width: 5rem;
  color: #1DF1EF;
}
header .header-nav .modos .icono-sol {
  display: none;
}
header .header-nav .modos .icono-luna {
  display: block;
  height: 2.5rem;
  margin: 0 3rem;
}
@media (min-width: 768px) {
  header .header-nav .modos .icono-luna {
    margin: 0;
  }
}
header .header-nav .header-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  header .header-nav .header-mobile {
    display: none;
  }
}
header .header-nav .header-mobile .icono-menu {
  cursor: pointer;
  width: 3rem;
  min-width: 3rem;
  display: flex;
}

.contenedor-section-principal {
  background-color: #083e3d;
  height: 80vh;
}

.section-principal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 0 1rem;
}
.section-principal .texto-presentacion {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.section-principal .texto-presentacion .saludo {
  color: #FFFFFF;
  font-size: 1.5rem;
  font-weight: 200;
}
.section-principal .texto-presentacion .saludo img {
  display: inline-block;
  width: 2rem;
}
@media (min-width: 480px) {
  .section-principal .texto-presentacion .saludo {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) {
  .section-principal .texto-presentacion .saludo {
    font-size: 2rem;
  }
}
.section-principal .texto-presentacion h1 {
  font-weight: 800;
  line-height: 1;
  margin: 1rem 0;
  font-size: 2.5rem;
}
@media (min-width: 480px) {
  .section-principal .texto-presentacion h1 {
    font-size: 3.5rem;
  }
}
.section-principal .texto-presentacion .descripcion {
  color: #FFFFFF;
