html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    
}
p {
    color: $negro;
    font-size: 2rem;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    @include tabletXL {
        max-width: 100rem;
    }
}

.bg-custom {
    background-image: url(/build/img/punto.webp);
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: $fuente_principal;
    margin: calc($separacion / 2) 0;
    font-weight: 300;
    text-align: center;
    margin: 0;
}

p {
    margin: 0;
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

ul {
    list-style: none;
}

.gradiente-letras {
    background-image: linear-gradient(90deg ,#1DF1EF, #EF1DF1);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}